@import url(https://fonts.googleapis.com/css2?family=Dela+Gothic+One&display=swap);
body {
  margin: 0 !important;
  padding: 0 !important;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #263238;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

h1 {
  font-family: 'Dela Gothic One', cursive;
  font-weight: 500;
  color: #ffffff;
  margin-top: 0px !important;
  padding-top: 0px !important;
}

div.buttons button {
  margin-inline: 2em;
  background-color: #4b5d67;
  color: white;
}

nav.sticky {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background-color: #4b5d67;
  z-index: 1;
  padding: 0;
  margin: 0;
}

div.card {
  background: #eceff1;
}
img {
  width: 70%;
  height: auto;
}

div.hidden {
  display: none;
}

input {
  background-color: #eceff1;
  transition: all 0.2s ease-in-out;
}

input:focus {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

div#scroll {
  overflow-y: scroll;
  height: 500px;
}

div.main {
  background: white;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  box-shadow: 0px 0px 8px 2px rgba(255, 255, 255);
}
div.container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
input.addUser {
  background: #4b5d67;
  max-width: 90%;
  width: 80%;
  color: white;
  transition: all 0.2s ease-in-out;
}
.inputWrapper label {
  float: left;
  margin-left: 2em;
  font-weight: bold;
  margin-top: 1rem;
}
input.addUser:hover {
  background: white;
  color: #0e1111;
  border: 1px solid #4b5d67;
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
}

input.addUser:focus {
  background: white;
  color: #0e1111;
  border: 1px solid #4b5d67;
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
}

button {
  background-color: #4b5d67;
}

div.container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
div.inner {
  background-color: #222831;
  width: 70%;
}

div.inner label {
  color: whitesmoke;
}

